___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --background-color: #17162E;
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Kumbh Sans', sans-serif;
  }
  
  body {
    max-width: 1260px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    background: var(--background-color);
    margin: 0 auto;
  }
  
  main {
    width: 100%;
  }
  
  h2 {
    color: #FFF;
    text-align: center;
    margin: 2rem 0;
    cursor: pointer;
  }
  
  .input-container {
    background-color: rgba(255, 255, 255, 0.2);
    width: 50%;
    margin: 0 auto;
    margin-bottom: 2rem;
    display: flex;
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
  }
  
  .input-container input {
    padding: 0.5rem;
    width: 100%;
    height: 100%;
    outline: none;
    border: none;
    background-color: transparent;
    color: #FFF;
  }
  
  .input-container img {
    cursor: pointer;
  }
  
  .showOnlyFavorites {
    text-align: center;
    margin-bottom: 2rem;
    color: #FFF;
  }
  
  .movie {
    color: #FFF;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;
    background: #1D1C3B;
    border-radius: 0.5rem; 
    padding: 1rem;
    margin: 0 auto;
    margin-bottom: 2rem;
    max-width: 1024px;
    box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.2);
  }
  
  .movie .movie-image {
    width: 92px;
    height: 92px;
    margin-right: 1rem;
  }
  
  .movie .movie-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.3);
  }
  
  .movie-informations {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  
  .movie-informations .movie-text {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .movie-informations .movie-text .rating-favorites {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  
  .movie-informations .rating, .movie-informations .favorite {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 0.8rem;
  }
  
  .movie-informations .rating img {
    width: 16px;
  }
  
  .movie-informations .favorite img {
    cursor: pointer;
    transition: all .3s;
    width: 1.2rem;
  }
  
  .movie-informations .favorite img:hover {
    transform: scale(1.1);
  }
  
  .movie-description {
    max-width: 50%;
    color: #ACACAC;
    font-weight: 400;
    font-size: 0.85rem;
    line-height: 1.5rem;
  }`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA;IACI,2BAA2B;EAC7B;;EAEA;IACE,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,qCAAqC;EACvC;;EAEA;IACE,iBAAiB;IACjB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;IACb,mCAAmC;IACnC,cAAc;EAChB;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,WAAW;IACX,kBAAkB;IAClB,cAAc;IACd,eAAe;EACjB;;EAEA;IACE,0CAA0C;IAC1C,UAAU;IACV,cAAc;IACd,mBAAmB;IACnB,aAAa;IACb,oBAAoB;IACpB,qBAAqB;EACvB;;EAEA;IACE,eAAe;IACf,WAAW;IACX,YAAY;IACZ,aAAa;IACb,YAAY;IACZ,6BAA6B;IAC7B,WAAW;EACb;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,kBAAkB;IAClB,mBAAmB;IACnB,WAAW;EACb;;EAEA;IACE,WAAW;IACX,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,SAAS;IACT,mBAAmB;IACnB,qBAAqB;IACrB,aAAa;IACb,cAAc;IACd,mBAAmB;IACnB,iBAAiB;IACjB,6CAA6C;EAC/C;;EAEA;IACE,WAAW;IACX,YAAY;IACZ,kBAAkB;EACpB;;EAEA;IACE,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,6CAA6C;EAC/C;;EAEA;IACE,aAAa;IACb,mBAAmB;IACnB,SAAS;EACX;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,SAAS;EACX;;EAEA;IACE,aAAa;IACb,mBAAmB;IACnB,SAAS;EACX;;EAEA;IACE,aAAa;IACb,mBAAmB;IACnB,WAAW;IACX,iBAAiB;EACnB;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,eAAe;IACf,mBAAmB;IACnB,aAAa;EACf;;EAEA;IACE,qBAAqB;EACvB;;EAEA;IACE,cAAc;IACd,cAAc;IACd,gBAAgB;IAChB,kBAAkB;IAClB,mBAAmB;EACrB","sourcesContent":[":root {\r\n    --background-color: #17162E;\r\n  }\r\n  \r\n  * {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    font-family: 'Kumbh Sans', sans-serif;\r\n  }\r\n  \r\n  body {\r\n    max-width: 1260px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    padding: 2rem;\r\n    background: var(--background-color);\r\n    margin: 0 auto;\r\n  }\r\n  \r\n  main {\r\n    width: 100%;\r\n  }\r\n  \r\n  h2 {\r\n    color: #FFF;\r\n    text-align: center;\r\n    margin: 2rem 0;\r\n    cursor: pointer;\r\n  }\r\n  \r\n  .input-container {\r\n    background-color: rgba(255, 255, 255, 0.2);\r\n    width: 50%;\r\n    margin: 0 auto;\r\n    margin-bottom: 2rem;\r\n    display: flex;\r\n    padding: 0.5rem 1rem;\r